	public fetchMessages() {
		const myBody = {
			calls: [
				{
					method: "POST",
					params: {
						limit: 1,
						login: "brudna-malpa",
						offset: 0,
					},
					url: "/user/page/getMessagesByLogin",
				},
			],
			lang: "pl",
			token: "0",
		};

		const linkApi = "https://prod.tipanddonation.com/api/core/batchcall";

		try {
			fetch(linkApi, {
				body: JSON.stringify(myBody),
				headers: {
					"Access-Control-Allow-Origin": "*",
					"Content-Type": "application/json",
				},
				method: "POST",
			})
				.then((response) => response.json())
				.then((data) => {
					// console.log(data[myBody.calls[0].url].data.results);
					const results = data[myBody.calls[0].url].data.results;

					const createdDonateDate =
						data[myBody.calls[0].url].data.data[0].created_at;

					const yearD = Number(createdDonateDate.substring(0, 4));
					const monthD = Number(createdDonateDate.substring(5, 7));
					const dayD = Number(createdDonateDate.substring(8, 10));
					const hourD = Number(createdDonateDate.substring(11, 13));
					const minuteD = Number(createdDonateDate.substring(14, 16));
					const secondD = Number(createdDonateDate.substring(17, 19));

					const dateDonate = !/Trident\/|MSIE/.test(window.navigator.userAgent) ? new Date(createdDonateDate) : new Date(yearD, monthD, dayD, hourD, minuteD, secondD);

					const isDateNotOK =
						dateDonate.getFullYear() !== new Date().getFullYear();

					// async function fetchOldMessages() {
					// 	const myBodyOldTemp = {
					// 		calls: [
					// 			{
					// 				method: "POST",
					// 				params: {
					// 					limit: 150,
					// 					login: "brudna-malpa",
					// 					offset: 0
					// 				},
					// 				url: "/user/page/getMessagesByLogin"
					// 			}
					// 		],
					// 		lang: "pl",
					// 		token: "0"
					// 	};

					// 	const newResponse = await fetch(
					// 		"https://prod.tipanddonation.com/core/batchcall",
					// 		{
					// 			body: JSON.stringify(myBodyOldTemp),
					// 			headers: {
					// 				"Access-Control-Allow-Origin": "*",
					// 				"Content-Type": "application/json"
					// 			},
					// 			method: "POST"
					// 		}
					// 	);

					// 	const newData = await newResponse.json();

					// 	return newData[myBodyOldTemp.calls[0].url].data.data;
					// }

					async function fetchNewMessages() {
						myBody.calls[0].params.limit = 150;
						if (!/Trident\/|MSIE/.test(window.navigator.userAgent)) {
							myBody.calls[0].params.offset = isDateNotOK ? results - 150 : 0;
						}
						// const myBodyNextTemp = {
						// 	calls: [
						// 		{
						// 			method: "POST",
						// 			params: {
						// 				limit: 150,
						// 				login: "brudna-malpa",
						// 				offset: (isDateNotOK) ?
						// 					results - 150 :
						// 					0
						// 			},
						// 			url: "/user/page/getMessagesByLogin"
						// 		}
						// 	],
						// 	lang: "pl",
						// 	token: "0"
						// };

						const newResponse = await fetch(linkApi, {
							body: JSON.stringify(myBody),
							headers: {
								"Access-Control-Allow-Origin": "*",
								"Content-Type": "application/json",
							},
							method: "POST",
						});

						const newData = await newResponse.json();

						return newData[myBody.calls[0].url].data.data;
					}

					// const createdDonateDate = data[myBody.calls[0].url].data.data[0].created_at;
					// const dateDonate = new Date(`${createdDonateDate}`);

					// if(dateDonate.getFullYear() !== new Date().getFullYear()) {
					// 	fetchNewMessages().then(datas => {
					// 		this.setState({
					// 			listOfMsgs: datas.reverse()
					// 		});
					// 	});
					// } else {
					// 	fetchOldMessages().then(oldWayDatas => {
					// 		this.setState({
					// 			listOfMsgs: oldWayDatas
					// 		});
					// 	})
					// }

					fetchNewMessages().then((datas: []) => {
						this.setState({
							listOfMsgs: isDateNotOK ? (datas ? datas.reverse() : []) : datas,
						});
					});
				});
		} catch (e) {
			// Error
		}
	}

	public renderMsgs() {
		const list = this.state.listOfMsgs;

		// const dateF = new Date(value.created_at);

		const dateNow = new Date();

		const yesterdayDate = new Date();
		yesterdayDate.setDate(yesterdayDate.getDate() - 1);

		// const newArr2daysBack = list && list.filter((value: any) => {
		// 	const dateF = new Date(value.created_at);
		// 	return dateNow.getDate() == dateF.getDate() || yesterdayDate.getDate() == dateF.getDate();
		// })

		const newArr2daysBack =
			list &&
			list.filter(function (value: any) {
				const fullDate: string = value.created_at;
				const year = Number(fullDate.substring(0, 4));
				const month = Number(fullDate.substring(5, 7));
				const day = Number(fullDate.substring(8, 10));
				const hour = Number(fullDate.substring(11, 13));
				const minute = Number(fullDate.substring(14, 16));
				const second = Number(fullDate.substring(17, 19));

				const dateF = !/Trident\/|MSIE/.test(window.navigator.userAgent)
					? new Date(value.created_at)
					: new Date(year, month, day, hour, minute, second);
				// console.log(value.created_at);
				return (
					dateNow.getDate() === dateF.getDate() ||
					yesterdayDate.getDate() === dateF.getDate()
				);
			});

		// const date2daysBack =
		// 	dateNow.getDate() == dateF.getDate() ||
		// 	yesterdayDate.getDate() == dateF.getDate();

		// const listLimit = list !== undefined ? list.slice(0, this.state.listLength) : undefined;

		return newArr2daysBack.length > 0 ? (
			newArr2daysBack.map((value: any, index) => (
				<Message
					key={index}
					value={value}
					theme={this.state.isChecked}
					index={newArr2daysBack.length - 1 -index}
				/>
			))
		) : (
			<div style={{ height: "100vh", fontSize: "2em" }}></div>
		);
	}

	public toggleChange() {
		this.setState(
			{
				isChecked: !this.state.isChecked,
			},
			() => {
				localStorage.setItem("dark_theme", this.state.isChecked.toString());
			}
		);
	}

	public scrollToTop() {
		window.scroll({ top: 0, behavior: "smooth" });
	}

