	public showOrHide(isClicked: boolean, container: HTMLDivElement, element: HTMLIFrameElement, classShow: string, classHide: string, source: string) {
		if (!isClicked) {
			if (container && element) {
				container.classList.add(classShow);
				container.classList.remove(classHide);
				if (element.src === "") {
					element.src = source;
				}
			}
		} else {
			if (container) {
				container.classList.add(classHide);
				container.classList.remove(classShow);
			}
		}

		this.setState({
			yt_clicked: !isClicked
		});
	}

