import React from "react";
import "./style.scss";

class Message extends React.PureComponent<any, any> {
	constructor(props: any) {
		super(props);

		this.state = {
			messageLogin: '',
			messageDate: '',
			messageSelected: false,
			storage_login: '',
			storage_date: ''
		}

		this.onSelectMessage = this.onSelectMessage.bind(this);
	}

	public componentDidMount() {
		const dateFromStorage = localStorage.getItem('message_date');
		const loginFromStorage = localStorage.getItem('message_login');
		
		this.setState({
			storage_login: loginFromStorage,
			storage_date: dateFromStorage
		})
	}

  public changeUrl() {
    const {value} = this.props;
		const {content} = value;

    const urls = content.replace("https:", "https://");
    const ytLinks = urls.replace("youtube.com", "youtube.com/");

    const picosongLinks = ytLinks.replace("picosong.com", "picosong.com/");

    const ytShortLinks = picosongLinks.replace("youtu.be", "youtu.be/");

    const sCLinks = ytShortLinks.replace("soundcloud.com", "soundcloud.com/");

    const finalSCLinks = sCLinks.replace("[...]", "/");

    return finalSCLinks;
  }

  public createMarkup(content: any) {
    return { __html: `${content}` };
	}
	
	public onSelectMessage(e: React.MouseEvent) {
		if(!(document.getSelection()!.type === 'Range')) {
		const tempLogin = e.currentTarget.firstChild!.firstChild!.textContent;
		const tempDate = e.currentTarget.firstChild!.nextSibling!.firstChild!.textContent;

		const allMessages = e.currentTarget.parentElement!.childNodes;

		allMessages.forEach(element => {
			const finalElement = element as HTMLElement;

			finalElement.classList.remove('selectDark');
			finalElement.classList.remove("selectLight");
		})

		localStorage.setItem('message_login', tempLogin!.toString());
		localStorage.setItem('message_date', tempDate!.toString());

		if (this.props.theme){
			e.currentTarget.classList.add("selectDark");
		} else {
			e.currentTarget.classList.add('selectLight');
		}

		this.setState({
			messageSelected: !this.state.messageSelect,
			messageLogin: tempLogin,
			messageDate: tempDate,
		});
	}
	}

  public render() {

		const {value, theme, index} = this.props;
		
		const {storage_login, storage_date} = this.state;

    const amountNetto = Math.fround(value.payment.amount_netto / 100).toFixed(2);

		const dateF = new Date(value.created_at);
		
		// const dateNow = new Date();

		const yesterdayDate = new Date();
		yesterdayDate.setDate(yesterdayDate.getDate() - 1);

    const day = dateF.getDate() < 10 ? `0${dateF.getDate()}` : dateF.getDate();
    const month = dateF.getMonth() + 1;
    const newMonth = month < 10 ? `0${month}` : month;

    const hour = dateF.getHours() < 10 ? `0${dateF.getHours()}` : dateF.getHours();
    const minute = dateF.getMinutes() < 10 ? `0${dateF.getMinutes()}` : dateF.getMinutes();
    const second = dateF.getSeconds() < 10 ? `0${dateF.getSeconds()}` : dateF.getSeconds();

    const newDate = `${day}-${newMonth}-${dateF.getFullYear()} ${hour}:${minute}:${second}`;

    const newContent = this.changeUrl();

    const urls = newContent.match(/\bhttps?:\/\/\S+/);

		const finalUrls = newContent.replace(urls, `<a href="${urls}">${urls}</a>`);
		
		// const date2daysBack = (dateNow.getDate() == dateF.getDate()) || (yesterdayDate.getDate() == dateF.getDate());

		const selectedColor = (value.login === storage_login) && (newDate === storage_date)
			? theme ? 'selectDark' : 'selectLight' : '';

    return (
      <div onClick={this.onSelectMessage} /*style={date2daysBack ? {}: {display: 'none'}}*/ className={`${selectedColor} message_container ${theme ? "message_container_dark_theme" : ""}`}>
        <div className="message_flex">
          <div className="message_login">{value.login}</div>
          <div className="message_currency">
            {amountNetto} {value.payment.currency}
          </div>
        </div>
        <div className="message_flex">
          <div className="message_date_prov">{newDate}</div>
          <div className="message_date_prov">
            Płatność: {value.payment.provider}
          </div>
        </div>
        <div className="message_content" dangerouslySetInnerHTML={this.createMarkup(finalUrls)} />
        <div className="numberOfDonate">{index + 1}</div>
      </div>
    );
  }
}

export default Message;
